
#canvas{
  width: 100vw;
  height: 100vh;
  /* background: rgba(177,207,242,255); */
  position: fixed;
  top: 0;
  left: 0;
  outline: none;
  background: linear-gradient(0deg, hsl(200, 50%,100%) 50%, hsl(214,80%,70%) 100%);


  cursor: move; /* fallback if grab cursor is unsupported */
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
}
#progress{
  width: 100px;
}
#progress-bar{
  width: 100px;
}
#icon{
  position: absolute;
  bottom: 10px;
  right: 20px;
  z-index: 10;
  border-radius: 50%;
  background-color: wheat;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
#icon:hover {
  background-color: rgb(13, 14, 13);
  color: wheat;
}
#drag-svg{
  width: 100vw;
 
  font-weight:bold ;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  margin-top: 2vh;
  top: 0;
  z-index: 99;
  
}
.canvas-container{
  overflow: hidden;
}
#drag-svg .container{
  font-size: larger;
  width: 200px;
  height: 100px;
  line-height: 10px;;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  
}

.svg {
margin-bottom: 6px;
  width: 30px;
  height: 30px;
  color: aliceblue;
  position: relative;
  animation-name: example;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

@keyframes example {
  0%   { left:-100px; top:0px;}
  50%  { left:100px; top:0px;}
  100%  { left:-100px; top:0px;}
 
}
.home-container{
 
  top: 0;
  left: 0;
  position: absolute;
  z-index: 99;
  background-color: black;
  color: white;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 5px;
  overflow: hidden;
}
.welcome h1{
  font-size: 100px;
}
.contents{
  width: 80vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
}
.contents h2{
  font-size: 40px;
  font-weight: lighter;
}
.btn{
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
.loader{

  bottom: 10px;
 right: 20px;
}
.stack{
 margin-top: 3%;

 bottom: 10px;
 right: 20px;

}
#b-lander{
  z-index: 99;
  position: absolute;
  top: 10px;
  left: 20px;
  font-size: 22px;
  font-weight: bolder;
}
#information{
  display: flex;
  flex-direction: column;
  width: 100vw;
  overflow-wrap: break-word;
  background-color: aqua;
  justify-content: center;
  align-items: center;
  
 

}
#information > div {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  flex-direction: column;
  flex-wrap: wrap;
  line-height: 50px;
  top: 50px;
  font-size: 20px;
  font-weight: 1px;
 
}

#loadingscreen {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: black;
  z-index: 998;
  color: white;
}

.title-content {
  font-size: 48px;
  font-weight: 400;
  line-height: 120%;
}

.title {
  font-size: 96px;
  font-weight: 600;
  line-height: 100%;
  text-transform: uppercase;
}

#loader-wrapper {
  position: absolute;
  width: 90%;
  max-width: 1080px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  row-gap: 40px;
  justify-content: space-between;
}

/* pages css start */

.extra p {
  font-size: 22px;
  line-height: 150%;
  font-weight: 400;
}

.extra a {
  color: #4a8f48 !important;
  text-decoration: none;
}

.extra h1 {
  font-size: 48px;
  font-weight: 400;
  line-height: 120%;
}

.extra h2 {
  font-size: 26px;
  font-weight: 600;
  line-height: 120%;
}
.extra-container{
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.extra {
  background-color: black;
  width: 50vw;
  color: white;
  
  margin: 10% 20px 10% 20px;
  display: flex !important;
  justify-content: center !important;
  align-items: start !important;
  flex-direction: column;
}



@media only screen and (max-width: 600px) {
  .extra {
    width: 100vw;
  }
  .extra h1 {
      font-size: 24px;
  }

  .extra h2 {
      font-size: 20px;
  }

  .extra p2 {
      font-size: 18px;
  }

  .extra p {
      font-size: 16px;
  }

  .helptext span {
      display: none;
    }
  .helptext:after {
      content:'Swipe to explore';
  }

  .modal-content-wrapper {
      margin: 32px 20px 48px 20px;
      row-gap: 40px;
  }

  .header {
      height: 360px;
  }

  .header-wrapper {
      margin: auto 20px auto 20px;
  }

  .title{
      font-size: 64px;
  }

  .title-content {
    font-size: 25px;
  }

}

/* pages scc end */

@media screen and (max-width: 1067px) and  (min-width: 700px){
  .welcome h1{
    font-size: 50px;
  }
  .contents h2{
    font-size: 25px;
   
  }
}
@media screen and (max-width: 701px) and  (min-width: 400px){
  .welcome h1{
    font-size: 40px;
  }
  .contents h2{
    font-size: 18px;
   
  }
}
@media screen and (max-width: 401px) and  (min-width: 150px){
  .welcome h1{
    font-size: 50px;
    flex-wrap: wrap;
  }
  .contents h2{
    font-size: 25px;
   
  }
  #information > div {
   
    line-height: 25px;
    top: 60px;
    font-size: 18px;
   
   
  }
  
}
.btn-home{
  background-color:#4a8f48 !important ;
  border-radius: 20px;
}
.btn-home:hover {
  background-color: aliceblue !important;
  color: black;
}