.hamburger-menu.open{
  height: 100vh !important;
}
.hamburger-menu {
  z-index: 997;
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100vh;
  margin: 0 0 0 -240px;
  padding: 30px;
  padding-top: 25px;
  background-color: #000000;
  transform: translateX(100%);
  transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0);
}

.hamburger-menu.open {
  transform: translateX(0);
}

.menu-toggle {
  top: 24px;
  right: 20px;
  padding: 5px 5px;
  cursor: pointer;
  width: 40px;
  height: 40px;
  z-index: 999;
  position: absolute
}

.bar {
  width: 30px;
  height: 3px;
  background-color: white;
  margin: 4px 0;
  transition: transform 0.8s ease, opacity 0.5s ease;
}

.menu {
  margin-top: 40px;
  list-style: none;
  padding: 20px;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;

}

.menu li {
  /* margin-bottom: 10px; */
  color: white;
  cursor: pointer;
  padding: 10px 0;
}

.menu li a {
  color: white;
  text-decoration: none;
  font-size: 24px;
}

.menu li a:hover {
  color: #4a8f48;
}

.menu-toggle.active .bar:nth-child(1) {
  transform: rotate(45deg) translate(5px, 6px);
}

.menu-toggle.active .bar:nth-child(2) {
  opacity: 0;
}

.menu-toggle.active .bar:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -6px);
}








